<template>
  <div class="text-center text-muted">{{(index+1).toString().padStart(Math.max(2,todoL), '0')}}</div>
  <div class="form-check">
    <input class="form-check-input pointer" @click="save(index, $event)" :checked="item.done?true:undefined"
      :id="'input-' + index" type="checkbox" value="" />
    <label class="form-check-label pointer" :class="[{'text-decoration-line-through': item.done === true}, item.status==='z'?'low':item.status]"
      :for="'input-' + index">{{ item.todo}}</label>
  </div>
  <div class="flex-fill pointer justify-content-center" :class="item.status==='z'?'low':item.status" :title="item.status==='z'?'low':item.status" @click="statusItem(index)"><i class="gg-move-task"></i></div>
  <div class="flex-fill pointer justify-content-center" @click="editItem(index)"><i class="gg-pen"></i></div>
  <div class="flex-fill pointer justify-content-center text-danger" @click="removeItem(index)"><i class="gg-trash"></i></div>
  <div class="flex-fill pointer justify-content-center" @click="infoItem(index)"><i class="gg-info"></i></div>
</template>
<script>
  export default {
    name: 'listItem',
    emits: ['priority', 'remove', 'edit', 'local', 'info'],
    props: {
      todoL: Number,
      item: Object,
      index: Number
    },
    methods: {
      statusItem(index) {
        this.$emit('priority', index)
      },
      removeItem(index) {
        this.$emit('remove', index)
      },
      editItem(index) {
        this.$emit('edit', index)
      },
      save(index, e) {
        this.$emit('local', index, e)
      },
      infoItem(index) {
        this.$emit('info', index)
      }
    }
  }
</script>
