    background: #fff;
    max-width: 800px;
  }

  .text-danger {
    min-width: 24px;
    text-align: center;
    display: inline-flex;
    place-content: center;
  }

  .stats {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    direction: rtl;
  }

  #info {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: #fff;
    overflow: scroll;
    text-align: right;
    z-index: 9999;
  }

  #info>div>div {
    display: flex;
    width: 100%;
    counter-reset: section;
    justify-content: space-between;
  }

  #info>div>div:first-of-type {
    text-align: left;
  }

  #info>div>div>div:last-of-type div::before {
    counter-increment: section;
    content: counter(section)". ";
    font-size: 10px;
  }

  #info>div>div:first-of-type div:last-child {
    flex: 1 1 auto;
    display: flex;
    justify-content: space-between;
  }

  #info>div {
    display: flex;
    flex-direction: column;
    width: min-content;
    margin: 0 auto;
    min-width: 330px;
    padding-bottom: 0 !important;
  }

  .hidden {
    display: none !important;
  }

  .form-control {
    scroll-margin-top: 1em;
  }

  button {
    text-transform: uppercase;
  }
</style>
