    box-sizing: border-box;
    position: relative;
    display: block;
    transform: scale(var(--ggs,1));
    width: 10px;
    height: 12px;
    border: 2px solid transparent;
    box-shadow:
        0 0 0 2px,
        inset -2px 0 0,
        inset 2px 0 0;
    border-bottom-left-radius: 1px;
    border-bottom-right-radius: 1px;
    margin-top: 4px
}
.gg-trash::after,
.gg-trash::before {
    content: "";
    display: block;
    box-sizing: border-box;
    position: absolute
}
.gg-trash::after {
    background: currentColor;
    border-radius: 3px;
    width: 16px;
    height: 2px;
    top: -4px;
    left: -5px
}
.gg-trash::before {
    width: 10px;
    height: 4px;
    border: 2px solid;
    border-bottom: transparent;
    border-top-left-radius: 2px;
    border-top-right-radius: 2px;
    top: -7px;
    left: -2px
}
  html {
    scroll-behavior: smooth;
  }

  .custom-table {
    display: grid;
    box-sizing: border-box;
    grid-template-columns: auto 1fr 48px 48px 48px 48px;
    border-right: 1px solid #eee;
    overflow: hidden;
  }

  .custom-table>div {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding: 0 .25em;
    border-bottom: 1px solid #eee;
    border-left: 1px solid #eee;
    place-items: center;
    display: flex;
  }

  .custom-table>div:nth-of-type(6n+3) {
    text-align: right;
  }

  .locked~.custom-table {
    grid-template-columns: auto 1fr 48px 0 0 0;
  }
.center {
place-content: center;
}
