    width: 100%;
  }

  .container .form-check .form-check-input {
    margin: 0.25em;
    min-width: 1em;
  }

  .container .form-check {
    margin: 0;
  }

  .pointer {
    cursor: pointer;
  }
  @media (max-width:480px)  { 
  .custom-table>div{line-height: 2}
  }
